@import "reset.less";
.clearFix:after{
	content:"";display:table;clear:both;
}
.clearFix{
	*zoom:1;
}
.mr40{
	margin-right: 40px;
}
.mr20{
	margin-right: 20px;
}
.fc9{
	color: #999;
}
h2{font-size: 30px; font-weight: normal;}
.pzTopPn{
	position: absolute;
	top:0;
	left: 0;
	width:100%;
	height:95px;
	background-color:rgba(46,52,61,0.85);
	filter:progid:DXImageTransform.Microsoft.gradient(enabled='true',startColorstr='#DD2e343d',endColorstr='#DD2e343d');
	.pzTopInner{
		width:1000px;
		margin: 0 auto;
		.logo{
			float: left;
			a{
				display: block;
				width:238px;
				height: 61px;
				margin-top: 15px;
				background: url(../images/logo.png);
			}
		}
		.search{float: right;}
		.userInfo{
			float: right;
			margin-top: 30px;
			color: #fefefe;
			font-size: 12px;
			line-height: 26px;
			img{
				border-radius: 50%;
				vertical-align: text-top;
				float: left;
				margin-right: 5px;
			}
		}
	}
	
}
:root .pzTopPn{
	filter:none \9;
}
.pzNavPn{
	position: absolute;
	width:100%;
	height:50px;
	top:95px;
	left: 0;
	z-index: 999;
	background-color:rgba(26,30,36,0.9);
	filter:progid:DXImageTransform.Microsoft.gradient(enabled='true',startColorstr='#DD1a1e24',endColorstr='#DD1a1e24');
	.pzNavInner{
		width:1000px;
		margin:0 auto;
		li{
			float: left;
			width:20%;
			height: 50px;
			a{
				display: block;
				color: #fff;
				font-size: 16px;
				line-height: 50px;
				text-align: center;
				text-decoration: none;
				&:hover{
					text-decoration:none;
				};
			}
		}
		.select{
			a{
				color: #3aaae3;
			}
		}
		.hover{
			background: rgba(0,0,0,.3);
			dd{
				height: 137px;
				padding-left: 60px;
				border-bottom: 1px solid #4b4d52;
				background: rgba(34,36,40,.9);
				color: #a3a3a4;
				position: relative;
				i{
					display: inline-block;
					position: absolute;
					top:40px;
					left: 26px;
					width:22px;
					height: 22px;
					background: #fff;
				}
				.topFont{
					color: #fff;
					padding-top: 40px;
					margin-bottom: 5px;
				}
			}

		}
	}
}
.pzBannerPn{
	height: 695px;
	background: url(../images/topBanner1.jpg) top center no-repeat;
	margin-bottom: 60px;
}
.pzMobilePn{
	width:1000px;
	margin: 0 auto 60px;
	.hd{
		text-align: center;
		margin-bottom: 65px;
	}
	.bd{
		li{
			float: left;
			width:25%;
			.inner{
				text-align: center;
				color: #999;
				img{
					margin-bottom: 40px;
				}
				.tit{
					font-size: 18px;
					color: #333;
					margin-bottom: 20px;
				}
			}
		}
	}
}
.pzNewSuccPn{
	background-color: #f3f3f3;
	padding-top:65px;
	padding-bottom:65px;
	.pzNewSuccInner{
		width:1000px;
		margin: 0 auto;
		.col{
			float: left;
			width:480px;
		}
		.col2{
			float: right;
			width:495px;
		}
		.innerLeft{
			padding-right: 115px;
		}
		h2{
			color: #555555;
			margin-bottom: 40px;
		}
		.list{
			li{
				border-left:4px solid #ddd;
				font-size: 18px;
				line-height: 36px;
				padding: 10px 15px;
				margin-bottom: 25px;
				color: #999;
			}
		}
		.picInner{
			background: #fff;
			padding:5px 0 0 5px;
			li{
				width:240px;
				float: left;
				background: #fff;
				margin-right: 5px;
				margin-bottom: 5px;
			}
		}
	}
}

.pzProfessorPn{
	background-color: #46a5d5;
	padding-top:65px;
	padding-bottom:65px;
	margin-bottom:65px;
	.pzProFessorInner{
		width: 1000px;
		margin:0 auto;
		color: #a3d2ea;
		.hd{
			margin-bottom: 78px;
			ul{
				text-align: center;
				font-size: 0;
			}
			li{
				display: inline-block;
				width:260px;
				font-size: 30px;
				cursor: pointer;
			}
			.select{
				color: #fff;
			}
		}
		.bd{
			ul{
				li{
					float: left;
					width:25%;
					.cirImg{ border:3px solid #fff; border-radius: 50%; width:154px; height: 154px;}
					.inner{
						text-align: center;
					}
					.icnTag{
						display: inline-block;
						vertical-align: text-top;
						width:149px;
						height: 30px;
						line-height: 30px;
						text-align: center;
						color: #fff;
						font-size: 16px;
						font-style: normal;
						background: url(../images/tag.png);
					}
					.star{
						font-size: 0;
						margin-bottom: 16px;
					}
					.icnStar{
						display: inline-block;
						width:17px;
						height: 16px;
						margin-right: 6px;
						vertical-align: text-top;
						cursor: pointer;
						background-image: url(../images/star.png);

					}
					.gray{
						background-image: url(../images/star2.png);
					}
					.name{
						font-size: 24px;
						color: #fff;
						margin-bottom: 20px;
					}
					.picCont{
						width:160px;
						height: 160px;
						position: relative;
						margin:0 auto 35px;
						.icnTag{
							position: absolute;
							bottom: 0;
							left: 5px;
						}

					}
				}
			}
		}
	}
	
}
.pzDynamicNewsPn{
	.pzDynamicInner{
		width:1000px;
		margin:0 auto;
		h2{
			margin-bottom: 50px;
			text-align: center;
		}
		.commentPn{
			float: left;
			width:300px;
			margin-right: 30px;
			margin-bottom: 35px;
			.commentHd{
				border-radius: 5px;
				background-color: #f3f3f3;
				padding:20px 15px;
				margin-bottom: 30px;
				position: relative;
				a{
					color: #3aaae3;
				}
				.cont{
					line-height: 27px;
					margin-bottom: 24px;
				}
				.info{
					color: #999;
					.icnTime{
						display: inline-block;
						width:16px;
						height: 16px;
						vertical-align: text-top;
						margin-top: 3px;
						margin-right: 10px;
						background: url(../images/time.png);
					}
					.icnCommet{
						display: inline-block;
						width:17px;
						height: 17px;
						vertical-align: text-top;
						margin-right: 10px;
						margin-top: 3px;
						background: url(../images/pl.png);
					}
				}
				.icnCir{
					display: inline-block;
					position: absolute;
					bottom: -16px;
					left: 28px;
					width:16px;
					height: 16px;	
					background: url(../images/comment.png);
				}
			}

			.commentBd{
				.name{
					font-size: 20px;
				}
			}
		}

	}
}

